export default {
    name:'center',
    computed:{
        // 直接获取
        loginname:function(){
            return this.$store.state.loginname;
        }
    },
    created:function(){
        // 跳转到用户页面，使用编程式路由
        // this.$router.push，此方法会向history添加一个记录，点击后退
        // this.$router.replace不会向history添加记录，上一个页面不存在
        this.$router.replace('/user/'+this.loginname)
    }
}



