// vuex状态管理器
import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations.js';
import actions from './actions.js';

//全局注册
Vue.use(Vuex);
//创建Store对象
const store = new Vuex.Store({
    // 状态数据
    state:{
        topics:[
            {name:'all',desc:'全部'},
            {name:'good',desc:'精华'},
            {name:'share',desc:'分享'},
            {name:'ask',desc:'问答'},
            {name:'job',desc:'招聘'},
            {name:'dev',desc:'客户端测试'}
          ],
          //actionsheet菜单
          //用户未登录时显示登录
          unLoginedMoreMenus:{
            login: '登录'
          },
            //用户已经登录时显示登出
          loginedMoreMenus:{
            create:'发表新话题',
            center:'个人中心',
            msg:'消息通知',
            logout:'登出'
          },
          // 登录状态
          isLogined:false,
          // 登录用户名
          loginname:'',
          // token码
          accesstoken:'',
          // 登录ID
          author_id:'',
          // actionsheet 显示与变量
          isShowMoreMenus:false,
    },
    // 状态数据的计算属性
    getters:{
        moreMenus:function(state){
            if(state.isLogined){
              return state.loginedMoreMenus;
            }
            return state.unLoginedMoreMenus;
          },
          getTopicDescByTab:function(state){
            return function(tab){
              for(var i=0;i<state.topics.length;i++){
                if(state.topics[i].name==tab){
                  return state.topics[i].desc;
                }
              }
            }
          }
    },
    // 修改同步数据的函数
    mutations,
    // 修改异步数据的函数
    actions

})
//对外输出
export default store ;



